<!-- PostDetail.vue -->
<template>
  <div class="detail-post" v-if="post">
    <div class="detail-header">
      <h1>{{ post.title }}</h1>
      <div class="detail-meta">
        <time :datetime="post.date">{{ post.date }}</time>
        <span class="detail-reading-time" v-if="post.readingTime">{{ post.readingTime }} 分钟阅读</span>
      </div>
    </div>
    
    <div class="detail-container">
      <div class="detail-toc" v-if="tableOfContents">
        <div class="detail-toc-title">目录</div>
        <div class="detail-toc-content" v-html="tableOfContents"></div>
      </div>
      
        <!-- 修改代码块的渲染部分 -->
      <div class="detail-content markdown-body" v-html="renderedContent" ref="contentRef">
      </div>
    </div>
  </div>
</template>
  
  <script>
  import { computed, onMounted, ref, watch, nextTick } from 'vue'
  import { useRoute } from 'vue-router'
  import { useStore } from 'vuex'
  import { marked } from 'marked'
  
  // 正确的 Prism.js 导入顺序
  import Prism from 'prismjs'
  import 'prismjs/themes/prism-tomorrow.css'
  // 在核心加载后再加载语言
  import 'prismjs/components/prism-markup'
  import 'prismjs/components/prism-css'
  import 'prismjs/components/prism-javascript'
  import 'prismjs/components/prism-typescript'
  import 'prismjs/components/prism-python'
  import 'prismjs/components/prism-bash'
  import 'prismjs/components/prism-json'
  // 可选的插件
  import 'prismjs/plugins/line-numbers/prism-line-numbers'
  import 'prismjs/plugins/line-numbers/prism-line-numbers.css'
  
  export default {
    name: 'PostDetailPage',
    setup() {
      const route = useRoute()
      const store = useStore()
      const contentRef = ref(null)
  
      const loadMathJax = () => {
        return new Promise((resolve) => {
          window.MathJax = {
            tex: {
              inlineMath: [['$', '$']],
              displayMath: [['$$', '$$']]
            },
            svg: {
              fontCache: 'global'
            },
            startup: {
              pageReady: () => {
                resolve()
              }
            }
          }
  
          const script = document.createElement('script')
          script.src = 'https://cdn.jsdelivr.net/npm/mathjax@3/es5/tex-svg.js'
          document.head.appendChild(script)
        })
      }
      const getImageUrl = (imagePath) => {
        if (!imagePath) return ''
        // 如果路径是以 http 开头的，直接返回
        if (imagePath.startsWith('http')) {
          return imagePath
        }
        // 否则使用 public 中的路径
        return `/posts/images/${imagePath}`
      }
      const configureMarked = () => {
        const renderer = new marked.Renderer()

        renderer.code = (code, language = 'plaintext') => {
          let highlightedCode = code
          try {
            if (Prism.languages[language]) {
              highlightedCode = Prism.highlight(
                code,
                Prism.languages[language],
                language
              )
            }

            if (language === 'math') {
              return `<div class="math-block">$$${code}$$</div>`
            }

            // 更新后的代码块模板，添加折叠功能
            return `
              <div class="code-block-wrapper">
                <div class="code-block-header">
                  <span class="code-block-language">${language}</span>
                  <div class="code-block-actions">
                    <button type="button" class="code-collapse-btn" title="折叠/展开">
                      <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="collapse-icon">
                        <polyline points="6 9 12 15 18 9"></polyline>
                      </svg>
                    </button>
                    <button type="button" class="code-copy-btn" title="复制代码">
                      <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="copy-icon">
                        <rect x="9" y="9" width="13" height="13" rx="2" ry="2"></rect>
                        <path d="M5 15H4a2 2 0 0 1-2-2V4a2 2 0 0 1 2-2h9a2 2 0 0 1 2 2v1"></path>
                      </svg>
                      <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="check-icon hidden">
                        <polyline points="20 6 9 17 4 12"></polyline>
                      </svg>
                    </button>
                  </div>
                </div>
                <div class="code-block-content">
                  <pre class="line-numbers"><code class="language-${language}">${highlightedCode}</code></pre>
                </div>
              </div>
            `;
          } catch (err) {
            console.error('Code highlighting error:', err)
            return `<pre><code>${code}</code></pre>`
          }
        }

        // 修改图片渲染逻辑
        renderer.image = (href, title, text) => {
          const imageUrl = getImageUrl(href)
          return `
            <div class="image-container">
              <img src="${imageUrl}" alt="${text}" title="${title || ''}" loading="lazy" />
              ${text ? `<div class="image-caption">${text}</div>` : ''}
            </div>
          `
        }
  
        marked.setOptions({
          renderer,
          gfm: true,
          breaks: true,
          pedantic: false,
          smartLists: true,
          smartypants: false
        })
      }
  
      const renderMathJax = () => {
        if (window.MathJax) {
          window.MathJax.typeset()
        }
      }
  
      const highlightCode = () => {
        if (contentRef.value) {
          const codeBlocks = contentRef.value.querySelectorAll('pre code')
          codeBlocks.forEach((block) => {
            Prism.highlightElement(block)
          })
        }
      }

      const initializeCodeActions = () => {
        // 处理代码复制
        const handleCodeCopy = async (event) => {
          const button = event.target.closest('.code-copy-btn')
          if (!button) return

          try {
            const code = decodeURIComponent(button.dataset.code)
            await navigator.clipboard.writeText(code)

            const copyIcon = button.querySelector('.copy-icon')
            const checkIcon = button.querySelector('.check-icon')
            copyIcon.classList.add('hidden')
            checkIcon.classList.remove('hidden')

            setTimeout(() => {
              copyIcon.classList.remove('hidden')
              checkIcon.classList.add('hidden')
            }, 2000)
          } catch (err) {
            console.error('Failed to copy code:', err)
          }
        }
          // 处理代码块折叠
        const handleCodeCollapse = (event) => {
          const button = event.target.closest('.code-collapse-btn')
          if (!button) return

          const wrapper = button.closest('.code-block-wrapper')
          const content = wrapper.querySelector('.code-block-content')
          const icon = button.querySelector('.collapse-icon')
          
          content.classList.toggle('collapsed')
          icon.classList.toggle('rotate-180')
        }
        // 添加事件监听
        const contentElement = contentRef.value
        if (contentElement) {
          contentElement.addEventListener('click', (event) => {
            if (event.target.closest('.code-copy-btn')) {
              handleCodeCopy(event)
            } else if (event.target.closest('.code-collapse-btn')) {
              handleCodeCollapse(event)
            }
          })
        }
      }


      onMounted(async () => {
        configureMarked()
        await loadMathJax()
        renderMathJax()
        initializeCodeActions()
      })

      const post = computed(() => 
        store.state.posts.find(p => p.id === parseInt(route.params.id))
      )
      const processMarkdownContent = (content) => {
        if (!content) return ''
        // 处理 Markdown 内容中的图片路径
        return content.replace(
          /!\[(.*?)\]\((.*?)\)/g,
          (match, alt, path) => {
            if (path.startsWith('http')) {
              return match
            }
            const imageUrl = getImageUrl(path)
            return `![${alt}](${imageUrl})`
          }
        )
      }
      const renderedContent = computed(() => {
        if (!post.value?.content) return ''
        try {
          // 先处理内容中的图片路径，再进行 Markdown 渲染
          const processedContent = processMarkdownContent(post.value.content)
          return marked.parse(processedContent)
        } catch (err) {
          console.error('Markdown rendering error:', err)
          return '<p>Error rendering content</p>'
        }
      })
    
      watch(renderedContent, () => {
        nextTick(() => {
          highlightCode()
          renderMathJax()
        })
      })
      
      return {
        post,
        renderedContent,
        contentRef
      }
    }
  }
  </script>
  
